.App {
  text-align: left;
  max-width: 700px;
  margin-left:auto;
  margin-right:auto;
  font-family: 'Noto Snas KR';
  font-size: 12pt;
}

.svg-inline{
  overflow: visible;
  display: inline-block;
  font-size: inherit;
  height: 1em;
  overflow: visible;
  vertical-align: -0.125em;
}
.logo_size{
  width: 15px;
}

a {
  background-color: initial;
  color: #007bff;
  text-decoration: none;
}
.btn {
  overflow: hidden;
  padding: 8px 15px;
  margin: 15px 5px 15px 5px;
  display: inline-block;
  font-size: 14px;
  outline: none;
  color: #fff;
  border-radius: 6px;
  font-weight: bold;
}
.btn:hover{
  cursor: pointer;
  transition: background-color 0.5s, color 0.5s;
}
.btnRed {
  background: #fff;
  border: 1.5px solid #ef4836;
  color: #ef4836;
  &:hover{
    background: #ef4836;
    border-color: #ef4836;
    color: #fff;
  }
  &:active{
    background: #ef4836;
    border-color: #fff;
  }
}
.btnBlue {
  background: #fff;
  border: 1.5px solid #007bff;
  color: #007bff;
  &:hover{
    background: #007bff;
    border-color: #007bff;
    color: #fff;
  }
  &:active{
    background: #007bff;
    border-color: #fff;
  }
}
.btnGreen {
  background: #fff;
  border: 1.5px solid #A6CE39;
  color: #A6CE39;
  &:hover{
    background: #A6CE39;
    border-color: #A6CE39;
    color: #fff;
  }
  &:active{
    background: #A6CE39;
    border-color: #fff;
  }
}
.btnPurple {
  background: #fff;
  border: 1.5px solid #cc00ff;
  color: #cc00ff;
  &:hover{
    background: #cc00ff;
    border-color: #cc00ff;
    color: #fff;
  }
  &:active{
    background: #cc00ff;
    border-color: #fff;
  }
}
.btnGrey {
  background: #fff;
  border: 1.5px solid #616161;
  color: #616161;
  &:hover{
    background: #616161;
    border-color: #616161;
    color: #fff;
  }
  &:active{
    background: #616161;
    border-color: #fff;
  }
}


.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
